
/* src/components/Todo.css */

body{
  background: rgb(18, 18, 18);
}
  
.todo-container{
  background: rgb(24, 24, 24);
  width: 40vw;
  margin: 10em auto;
  /* border-radius: 15px; */
  padding: 20px 10px;
  color: white;
  border: 5px solid rgb(36, 110, 194);
}
     
.task{
  border: 3px solid white;
  border-radius: 5px;
  padding: 0.5em;
  margin: 0.5em;
}
  
.task button{
  background: rgb(12, 124, 251);
  border-radius: 5px;
  margin: 0px 5px;
  padding: 3px 5px;
  border: none;
  cursor: pointer;
  color: white;
  float: right;
}
  
.header{
  margin: 0.5em;
  font-size: 2em;
  text-align: center;
}
  
.create-task input[type=text]{
  margin: 2.5em 2em;
  width: 90%;
  outline: none;
  border: none;
  padding: 0.7em;
  text-align: center;
}